.checkbox-container {
  position: relative;
  display: block;
  cursor: pointer;
  font-size: 16px;
  user-select: none; }
  .checkbox-container[data-checkbox-size='large'] {
    width: 48px;
    height: 48px; }
  .checkbox-container[data-checkbox-size='regular'] {
    width: 24px;
    height: 24px; }

.checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }
  .checkbox[data-checkbox-type='radio'] {
    border-radius: 50%; }
  .checkbox-checkmark {
    position: absolute;
    top: 0;
    left: 0;
    background-color: transparent;
    border: 2px solid #003164; }
    .checkbox-checkmark[data-checkbox-type='radio'] {
      border-radius: 50%; }
    .checkbox-checkmark[data-checkbox-size='large'] {
      width: 48px;
      height: 48px; }
    .checkbox-checkmark[data-checkbox-size='regular'] {
      width: 24px;
      height: 24px; }
    .checkbox-checkmark:after, .checkbox-checkmark:before {
      content: '';
      position: absolute;
      display: none;
      opacity: 0; }

[data-checkbox-size='large'] input:checked ~ .checkbox-checkmark:after,
[data-checkbox-size='large'] input:checked ~ .checkbox-checkmark:before {
  display: block;
  background-color: #003164;
  opacity: 1; }

[data-checkbox-size='large'] input:checked ~ .checkbox-checkmark:after {
  transform: rotate(45deg);
  width: 15px;
  height: 2px;
  top: 55%;
  left: 13%; }

[data-checkbox-size='large'] input:checked ~ .checkbox-checkmark:before {
  transform: rotate(135deg);
  width: 25px;
  height: 2px;
  top: 45%;
  left: 34.5%; }

[data-checkbox-size='regular'] input:checked ~ .checkbox-checkmark:after,
[data-checkbox-size='regular'] input:checked ~ .checkbox-checkmark:before {
  display: block;
  background-color: #003164;
  opacity: 1; }

[data-checkbox-size='regular'] input:checked ~ .checkbox-checkmark:after {
  transform: rotate(45deg); }

[data-checkbox-size='regular'] input:checked ~ .checkbox-checkmark:before {
  transform: rotate(135deg); }
