//
// Color
//

// basic
$color-primary-darkest                : #003164;
$color-primary-darker                 : #00A7FF;
$color-primary-dark                   : #E5F3FF;
$color-primary                        : #FF4384;
$color-primary-light                  : #FFEDF7;


$color-danger                         : rgb(213, 049, 049); // #d53131: reddish
$color-warn                           : rgb(239, 132, 060); // #ef843c: dusty-orange

// gray
$gray-black                           : rgb(000, 000, 000); // #000000
$gray-darkest                         : rgb(097, 103, 111); // #61676f: slate-grey
$gray-darker                          : rgb(129, 136, 145); // #818891: steel
$gray-dark                            : rgb(170, 178, 188); // #aab2bc: light-grey-blue
$gray                                 : rgb(186, 196, 211); // #bac4d3: cloudy-blue
$gray-light                           : rgb(223, 225, 230); // #dfe1e6: light-gray
$gray-lighter                         : rgb(243, 245, 246); // #f3f5f6: ice-gray
$gray-lightest                        : rgb(241, 243, 249); // #f1f3f9: light-pale-grey
$gray-white                           : rgb(255, 255, 255); // #ffffff


//
// Transition
//

$transition-mode                      : cubic-bezier( .25, .8, .25, 1);


//
// Texting
//

$font-family                          : "system", "-apple-system", "PingFang-TC", "Segoe UI", "Microsoft JhengHei", "Roboto", "Oxygen", "Fira Sans", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";

$font-size-xl                         : 20px;
$font-size-lg                         : 18px;
$font-size-md                         : 16px;
$font-size-sm                         : 14px;
$font-size-xs                         : 12px;


//
// Usage
//
$mobile-width: 375px;
$table-width: 750px;
$laptop-width: 992px;
$desktop-width: 1200px;

//
// RWD
//

@mixin above-mobile() {
  @media screen and (min-width: $mobile-width) {
    @content;
  }
}

@mixin above-table() {
  @media screen and (min-width: $table-width) {
    @content;
  }
}

@mixin above-laptop() {
  @media screen and (min-width: $laptop-width) {
    @content;
  }
}

@mixin above-desktop() {
  @media screen and (min-width: $desktop-width) {
    @content;
  }
}

$types: (
  default: (
    icon-color: $color-primary-dark,
    animation-color: $color-primary,
  ),
  primary: (
    icon-color: $color-primary-darker,
    animation-color: $color-primary-darker,
  )
);

@each $type, $styles in $types {
  &[data-timer-type='#{$type}'] {
    &.timer-svg {
      border: 4px solid map-get($styles, animation-color);
    }
  }
}

.timer-svg {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 540px;
  height: 540px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50;
  transform: translate(-50%, -50%) rotate(270deg);
  @each $type, $styles in $types {
    &[data-timer-type='#{$type}'] {
      .timer-svg__animation {
        &.active {
          stroke: map-get($styles , animation-color );
        }
      }
      .timer-svg__inner {
        border: 4px solid map-get($styles, animation-color);
        background-color: map-get($styles, animation-color );
      }
      &[data-is-playing='true'] {
        border: 4px solid map-get($styles, animation-color);
        .timer-svg__inner {
          background-color: #fff;
        }
        .play-icon {
          color: map-get($styles, animation-color);
        }
        .timer-svg__reset {
          background-color: map-get($styles, animation-color);
        }

      }

    }
  }
  &__inner {
    width: 500px;
    height: 500px;
    border-radius: 50%;
    transition: all 0.4s linear;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__reset {
    position: absolute;
    top: 58%;
    left: 40%;
    width: 14px;
    height: 14px;
    background-color: #fff;
    cursor: pointer;
  }
  .play-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 96px;
    height: 96px;
    transform: translate(-50%, -50%);
    color: #fff;
    cursor: pointer;
    i {
      width: 96px;
      height: 96px;
      font-size: 96px;
      transform: rotate(90deg);
    }
  }
  &__animation {

    stroke-width: 21;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    circle {
      stroke-dasharray: 360%;
      stroke-dashoffset: 57%;
      transition: all .5s linear;
      fill: none;
    }
  }
}

