//
// Color
//

// basic
$color-primary-darkest                : #003164;
$color-primary-darker                 : #00A7FF;
$color-primary-dark                   : #E5F3FF;
$color-primary                        : #FF4384;
$color-primary-light                  : #FFEDF7;


$color-danger                         : rgb(213, 049, 049); // #d53131: reddish
$color-warn                           : rgb(239, 132, 060); // #ef843c: dusty-orange

// gray
$gray-black                           : rgb(000, 000, 000); // #000000
$gray-darkest                         : rgb(097, 103, 111); // #61676f: slate-grey
$gray-darker                          : rgb(129, 136, 145); // #818891: steel
$gray-dark                            : rgb(170, 178, 188); // #aab2bc: light-grey-blue
$gray                                 : rgb(186, 196, 211); // #bac4d3: cloudy-blue
$gray-light                           : rgb(223, 225, 230); // #dfe1e6: light-gray
$gray-lighter                         : rgb(243, 245, 246); // #f3f5f6: ice-gray
$gray-lightest                        : rgb(241, 243, 249); // #f1f3f9: light-pale-grey
$gray-white                           : rgb(255, 255, 255); // #ffffff


//
// Transition
//

$transition-mode                      : cubic-bezier( .25, .8, .25, 1);


//
// Texting
//

$font-family                          : "system", "-apple-system", "PingFang-TC", "Segoe UI", "Microsoft JhengHei", "Roboto", "Oxygen", "Fira Sans", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";

$font-size-xl                         : 20px;
$font-size-lg                         : 18px;
$font-size-md                         : 16px;
$font-size-sm                         : 14px;
$font-size-xs                         : 12px;


//
// Usage
//
$mobile-width: 375px;
$table-width: 750px;
$laptop-width: 992px;
$desktop-width: 1200px;

//
// RWD
//

@mixin above-mobile() {
  @media screen and (min-width: $mobile-width) {
    @content;
  }
}

@mixin above-table() {
  @media screen and (min-width: $table-width) {
    @content;
  }
}

@mixin above-laptop() {
  @media screen and (min-width: $laptop-width) {
    @content;
  }
}

@mixin above-desktop() {
  @media screen and (min-width: $desktop-width) {
    @content;
  }
}

$sizes: (
  large: (
    checkbox-width: 48px,
    checkbox-height: 48px
  ),
  regular: (
    checkbox-width: 24px,
    checkbox-height: 24px
  )
);

$checkmark: (
  large: (
    width-before: 25px,
    height-before: 2px,
    position-top-before: 45%,
    position-left-before: 34.5%,
    width-after: 15px,
    height-after: 2px,
    position-top-after: 55%,
    position-left-after: 13%
  ),
  regular: (
    checkmark-width: 5px,
    checkmark-height: 10px,
    checkmark-position-top: 45%,
    checkmark-position-left: 34.5%
  )
);

.checkbox-container {
  position: relative;
  display: block;
  cursor: pointer;
  font-size: $font-size-md;
  user-select: none;
  @each $size, $styles in $sizes {
    &[data-checkbox-size='#{$size}'] {
      width: map-get($styles, checkbox-width);
      height: map-get($styles, checkbox-height);
    }
  }
}
.checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
  &[data-checkbox-type='radio'] {
    border-radius: 50%;
  }
  &-checkmark {
    position: absolute;
    top: 0;
    left: 0;
    background-color: transparent;
    border: 2px solid $color-primary-darkest;
    &[data-checkbox-type='radio'] {
      border-radius: 50%;
    }
    @each $size, $styles in $sizes {
      &[data-checkbox-size='#{$size}'] {
        width: map-get($styles, checkbox-width);
        height: map-get($styles, checkbox-height);
      }
    }
    &:after,
    &:before {
      content: '';
      position: absolute;
      display: none;
      opacity: 0;
    }
  }
}

@each $type, $styles in $checkmark {
  &[data-checkbox-size='#{$type}'] {
    input:checked {
      ~ .checkbox-checkmark:after,
      ~ .checkbox-checkmark:before {
        display: block;
        background-color: $color-primary-darkest;
        opacity: 1;
      }
      ~ .checkbox-checkmark:after {
        transform: rotate(45deg);
        width: map-get($styles, width-after);
        height: map-get($styles, height-after);
        top: map-get($styles, position-top-after);
        left: map-get($styles, position-left-after);
      }
      ~ .checkbox-checkmark:before {
        transform: rotate(135deg);
        width: map-get($styles, width-before);
        height: map-get($styles, height-before);
        top: map-get($styles, position-top-before);
        left: map-get($styles, position-left-before);
      }
    }
  }
}

