//
// Color
//

// basic
$color-primary-darkest                : #003164;
$color-primary-darker                 : #00A7FF;
$color-primary-dark                   : #E5F3FF;
$color-primary                        : #FF4384;
$color-primary-light                  : #FFEDF7;


$color-danger                         : rgb(213, 049, 049); // #d53131: reddish
$color-warn                           : rgb(239, 132, 060); // #ef843c: dusty-orange

// gray
$gray-black                           : rgb(000, 000, 000); // #000000
$gray-darkest                         : rgb(097, 103, 111); // #61676f: slate-grey
$gray-darker                          : rgb(129, 136, 145); // #818891: steel
$gray-dark                            : rgb(170, 178, 188); // #aab2bc: light-grey-blue
$gray                                 : rgb(186, 196, 211); // #bac4d3: cloudy-blue
$gray-light                           : rgb(223, 225, 230); // #dfe1e6: light-gray
$gray-lighter                         : rgb(243, 245, 246); // #f3f5f6: ice-gray
$gray-lightest                        : rgb(241, 243, 249); // #f1f3f9: light-pale-grey
$gray-white                           : rgb(255, 255, 255); // #ffffff


//
// Transition
//

$transition-mode                      : cubic-bezier( .25, .8, .25, 1);


//
// Texting
//

$font-family                          : "system", "-apple-system", "PingFang-TC", "Segoe UI", "Microsoft JhengHei", "Roboto", "Oxygen", "Fira Sans", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";

$font-size-xl                         : 20px;
$font-size-lg                         : 18px;
$font-size-md                         : 16px;
$font-size-sm                         : 14px;
$font-size-xs                         : 12px;


//
// Usage
//
$mobile-width: 375px;
$table-width: 750px;
$laptop-width: 992px;
$desktop-width: 1200px;

//
// RWD
//

@mixin above-mobile() {
  @media screen and (min-width: $mobile-width) {
    @content;
  }
}

@mixin above-table() {
  @media screen and (min-width: $table-width) {
    @content;
  }
}

@mixin above-laptop() {
  @media screen and (min-width: $laptop-width) {
    @content;
  }
}

@mixin above-desktop() {
  @media screen and (min-width: $desktop-width) {
    @content;
  }
}

$types: (
  default: (
    filled-left-color: $color-primary-light,
    filled-right-color: $color-primary-darkest,
    text-color: $color-primary,
    body-background:
      linear-gradient(
        90deg,
        $color-primary-light 29%,
        $color-primary-darkest 73%
      )
  ),
  primary: (
    filled-left-color: $color-primary-dark,
    filled-right-color: $color-primary-darkest,
    text-color: $color-primary-darker,
    body-background:
      linear-gradient(
        90deg,
        $color-primary-dark 29%,
        $color-primary-darkest 73%
      )
  )
);
@each $type, $styles in $types {
  body {
    &[data-home-type='#{$type}'] {
      background: map-get($styles, body-background);
    }
  }
}

.home {
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;

  @each $type, $styles in $types {
    &[data-home-type='#{$type}'] {
      .home-left {
        background-color: map-get($styles, filled-left-color);
      }
      .home-right {
        background-color: map-get($styles, filled-right-color);
      }
      .home-list__text {
        color: map-get($styles, text-color )
      }
    }
  }
  &-left {
    width: calc(100% - 450px);
    box-sizing: border-box;
    padding: 48px 85px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }
  &-dot {
    font-size: 14px;
    color: $color-primary-darkest;
  }
  &-input {
    width: 445px;
  }
  &-list {
    width: 445px;
    min-height: 160px;
    &-container {
      max-height: 145px;
      overflow: hidden;
    }

    &__text {
      text-align: right;
      cursor: pointer;
      transition: opacity 0.3s linear;
      &:hover {
        opacity: 0.5;
      }
    }
  }
  &-right {
    position: relative;
    width: 450px;
    height: 100%;
  }
}
